*{
    margin: 0;
    padding: 0;
}
html,body{
    width:100%;
    height:100%;
    box-sizing: border-box;
}
.mainHeader{
    background-color: rgb(54, 139, 115);
    width:100%;
    height:60px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.mainHeader .heading{
    margin-left: 10px;
}
.mainHeader{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    
}
.items{
    display: flex;
    align-items: center;
    gap:20px;
    cursor: pointer;
    margin-right: 20px;
}
.items a{
    text-decoration: none;
    font-weight: bold;
    color: black;
}
.items a:hover{
    opacity: 0.9;
    scale: 1.1;
    border-bottom: 1px solid black;
}
.menuLogo{
    visibility: hidden;
    width:50px;
    height:50px;
    position: absolute;
    right:3px;
   
   
    
}
#sidediv{
    color:black;
  width:50vw;
  height:60vh;
  background-color:red ;
  position: absolute;
  top:50px;
  right:-100px;
  z-index: 1;
  border-radius: 10px;
 visibility: hidden;
  animation: 2s ;
}
ul{
    display: none;
     position: absolute;
    right:0;
    top:7%;
    width:200px;
    height:400px;
    background-color: rgba(24, 180, 180, 0.9);
    border-radius: 5%;
    animation: shubh 0.5s;
}

ul li  a{
    text-decoration: none;
}
@keyframes shubh  {
    from{
        right: -40%;
    }
    to{
        right:0;
    }
}
 li{
    text-decoration:none;
    font-size: 25px;
    color:black;
}
.open{
    display: flex;
    align-items:center;
    justify-content: space-evenly;
    flex-direction: column;
}
.sidemenu{
    position: absolute;
    right:0;
    top:10%;
    width:50px;
    height:200px;
    background-color: red;
}
@keyframes side {
    to{
        right:-50px;
    }
}

@media only screen and (max-width: 768px){
    .mainHeader{
        width: 100%;
        justify-content:space-evenly;
    }
    .items{
        visibility:hidden;
    }
    .items a{
        font-size: 2.2vw;
    }
    .menuLogo{
        visibility: visible;
        margin-right: 10px;
       
    }
    #sidediv{
        visibility: hidden;
    }
    
}